.loader {
  width: 68px;
  height: 68px;
  box-sizing: border-box;
  border: 3px solid rgba(236, 233, 56, .98);
  border-style: solid solid dotted dotted;
  border-radius: 50%;
  margin-top: 80px;
  margin-left: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  transform-origin: center;
  border: 3px solid #274ecf;
  border-bottom-style: dotted;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack 1s linear infinite;
  position: absolute;
  top: 30px;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.breed-select {
